<template>
  <div id="app-edit">
    <AppEditWrapper id="app-edit-wrapper" :header="'–†–µ–¥–∞–∫—Ç–æ—Ä –∫–ª–∏–µ–Ω—Ç–∞: ' + editedCustomer.name">
      <template #nav-buttons>
        <CustomerNavigation :customer="editedCustomer" @save-customer="saveEditedCustomer(editedCustomer)"/>
      </template>

      <template #edit-section>
        <div class="edit-block">
          <AppHeaderIcon class="edit-block-header" header-level="4" material-icon="import_contacts" header-text="–û—Å–Ω–æ–≤–Ω—ã–µ –¥–∞–Ω–Ω—ã–µ"/>

          <div class="edit-block-content">
            <AppLineText
              inputID="input-city"
              label="–ù–∞–∑–≤–∞–Ω–∏–µ –∫–ª–∏–µ–Ω—Ç–∞: "
              maxLength="20"
              v-model="editedCustomer.name"
            />
          </div>
        </div>

        <div class="edit-block">
          <AppHeaderIcon class="edit-block-header" header-level="4" material-icon="chrome_reader_mode" header-text="–ó–∞–º–µ—Ç–∫–∏"/>

          <div class="edit-block-content">
            <AppNumbers
              inputID="input-note-count"
              label="–ó–∞–º–µ—Ç–æ–∫ –≤ —Å–ø–∏—Å–∫–µ:  "
              minValue="0"
              maxValue="35"
              maxLength="3"
              v-model="editedCustomer.notesCount"
            />

            <AppLineText
              placeholder="–í–≤–µ–¥–∏—Ç–µ –Ω–æ–≤—É—é –∑–∞–º–µ—Ç–∫—É"
              inputID="input-note"
              label="–ù–æ–≤–∞—è –ó–∞–º–µ—Ç–∫–∞: "
              maxLength="35"
              v-model="note"
            />

            <AppNotesList :notes-list="editedCustomer.notes" :notes-count="editedCustomer.notesCount"/>
          </div>
        </div>
        <div class="edit-block" id="subdivisions">
          <!-- <div class="subdivisions-wrapper">
            <AppHeaderIcon class="edit-block-header" header-level="4" material-icon="domain" header-text="–ü–æ–¥—Ä–∞–∑–¥–µ–ª–µ–Ω–∏—è"/>
            <ul class="subdivisions">
              <li class="line-text-label" v-for="subdivision in editedCustomer.subdivisions" :key="subdivision.id">{{ subdivision.name }}</li>
            </ul>
            <SubdivisionAdd
            @add-element="addCustomer"
            placeholder="–î–æ–±–∞–≤–∏—Ç—å –Ω–æ–≤–æ–µ –ø–æ–¥—Ä–∞–∑–¥–µ–ª–µ–Ω–∏–µ"
            />
          </div> -->
          <AppHeaderIcon class="edit-block-header" header-level="4" material-icon="domain" header-text="–ü–æ–¥—Ä–∞–∑–¥–µ–ª–µ–Ω–∏—è"/>
      <CustomerSubdivisionsTable
        @remove-customer="removeCustomer"
        :customers="customerSubdivisions"
        :customerId="customerId"
      />
      <SubdivisionAdd
        @add-element="addCustomer"
        placeholder="–î–æ–±–∞–≤–∏—Ç—å –Ω–æ–≤–æ–µ –ø–æ–¥—Ä–∞–∑–¥–µ–ª–µ–Ω–∏–µ"
      />
        </div>
      </template>
    </AppEditWrapper>
  </div>
</template>

<script>
import CustomerSubdivisionsTable from './subdivisions/CustomerSubdivisionsTable'
import { mapGetters, mapMutations } from 'vuex'
import firebase from 'firebase/app'
import AppEditWrapper from '@/components/edit/AppEditWrapper'
import CustomerNavigation from './CustomerNavigation'
import AppNumbers from '@/components/AppNumbers'
import AppLineText from '@/components/AppLineText'
import AppNotesList from '@/components/edit/AppNotesList'
import AppHeaderIcon from '../../AppHeaderIcon'
import SubdivisionAdd from '@/components/InputAdd'

export default {
  name: 'EditorWorkers',

  components: {
    CustomerSubdivisionsTable,
    SubdivisionAdd,
    AppEditWrapper,
    CustomerNavigation,
    AppNumbers,
    AppLineText,
    AppNotesList,
    AppHeaderIcon
  },

  data () {
    return {
      editedCustomer: '',
      note: ''
    }
  },

  computed: {
    ...mapGetters([
      // 'SET_CUSTOMERS_FROM_SERVER',
      // 'SET_CUSTOMERS_FROM_LOCAL_STORAGE',
      // 'SET_EMPLOYEES_FROM_LOCAL_STORAGE',
      'customers',
      'authEmployee'
    ]),

    customerId () {
      return String(this.$route.params.id)
    },

    currentCustomer () {
      return this.customers[this.customerId]
    },

    customerSubdivisions () {
      return this.currentCustomer.subdivisions
    }
  },

  created () {
    this.SET_CUSTOMERS_FROM_LOCAL_STORAGE()
    this.SET_EMPLOYEES_FROM_LOCAL_STORAGE()
    this.editedCustomer = this.customers[this.$route.params.id]
  },

  methods: {
    ...mapMutations([
      'SET_CUSTOMERS_FROM_SERVER',
      'SET_CUSTOMERS_FROM_LOCAL_STORAGE',
      'SET_EMPLOYEES_FROM_LOCAL_STORAGE'
    ]),

    editorExit () {
      this.$router.push('/customers')
    },

    saveEditedCustomer (customer) {
      if (this.note.length) {
        try {
          this.editedCustomer.notes.push(`${new Date().toLocaleDateString()}: ${this.note}`)
        } catch (error) {
          this.editedCustomer.notes = []
          this.editedCustomer.notes.push(`${new Date().toLocaleDateString()}: ${this.note}`)
        }
      }

      firebase.database().ref('customers/' + customer.id).set(customer)
        .then(() => {
          this.SET_CUSTOMERS_FROM_SERVER()
          this.editorExit()
        })
    },
    removeCustomer (id) {
      // eslint-disable-next-line eqeqeq
      if (id == 0) {
        // eslint-disable-next-line no-undef
        M.toast({ html: '–ì–ª–∞–≤–Ω–æ–µ –ø–æ–¥—Ä–∞–∑–¥–µ–ª–µ–Ω–∏–µ –Ω–µ–ª—å–∑—è —É–¥–∞–ª—è—Ç—å!' })
      } else {
        firebase.database().ref('/customers/' + this.customerId + '/subdivisions/' + id).remove()
          .then(() => {
            this.SET_CUSTOMERS_FROM_SERVER()
            console.log('–ü–æ–¥—Ä–∞–∑–¥–µ–ª–µ–Ω–∏–µ —É–¥–∞–ª—ë–Ω–æ üóëÔ∏è')
          })
      }
    },

    addCustomer (name) {
      // Body New Customer
      const newSubdivision = {
        id: Date.now(),
        name,
        contractNumber: '',
        contractDate: '',
        number: '',
        fax: '',
        site: '',
        email: '',
        address: '',
        notes: ['–ù–æ–≤–æ–µ –ø–æ–¥—Ä–∞–∑–¥–µ–ª–µ–Ω–∏–µ'],
        manager: this.authEmployee.surname + ' ' + this.authEmployee.name,
        status: '–î–µ–π—Å—Ç–≤—É—é—â–∏–π'
      }

      firebase.database().ref('/customers/' + this.customerId + '/subdivisions/' + newSubdivision.id).set(newSubdivision)
        .then(() => {
          this.SET_CUSTOMERS_FROM_SERVER()
          console.log('–ü–æ–¥—Ä–∞–∑–¥–µ–ª–µ–Ω–∏–µ –¥–æ–±–∞–≤–ª–µ–Ω–æ ‚ûï')
        })
    },

    updateCustomers () {
      this.SET_CUSTOMERS_FROM_SERVER()
      // eslint-disable-next-line no-undef
      M.toast({ html: '–ù–∞—à–∏ –∫–ª–∏–µ–Ω—Ç—ã –æ–±–Ω–æ–≤–ª–µ–Ω—ã' })
    }
  }
}
</script>

<style>

#app-edit-wrapper {
  grid-template-columns: 1fr !important;
}

.line-text-label {
    display: block;
    font-size: 1.2rem;
    color: #3e3e3e;
    line-height: 1.1rem;
    white-space: nowrap;
    max-width: 40%;
    padding-bottom: 0.45rem !important;
    border-bottom: 1px solid #3e3e3e;
}

#subdivisions {
      grid-column-start: 1;
    grid-column-end: -1;
}

</style>
